@-webkit-keyframes blink-effect {
  0% {
    opacity: 1;
    background-color: #fff;
  }
  50% {
    opacity: 0;
    background-color: #fff;
  }
  100% {
    opacity: 1;
    background-color: #fff;
  }
}

@keyframes blink-effect {
  0% {
    opacity: 1;
    background-color: #fff;
  }
  50% {
    opacity: 0;
    background-color: #fff;
  }
  100% {
    opacity: 1;
    background-color: #fff;
  }
}
@-webkit-keyframes loadEffect1 {
  0% {
    opacity: 0;
    transform: translateX(0px);
    display: none;
  }
  100% {
    opacity: 1;
    transform: translateX(79px);
  }
}
@keyframes loadEffect1 {
  0% {
    opacity: 0;
    transform: translateX(0px);
    display: none;
  }
  100% {
    opacity: 1;
    transform: translateX(79px);
  }
}
@-webkit-keyframes fadeEffect1 {
  0% {
    opacity: 1;
    transform: translateX(0px);
    display: block;
  }
  100% {
    opacity: 0;
    transform: translateX(-79px);
  }
}
@keyframes fadeEffect1 {
  0% {
    opacity: 1;
    transform: translateX(0px);
    display: block;
  }
  100% {
    opacity: 0;
    transform: translateX(-79px);
  }
}
@-webkit-keyframes eyeEffect {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes eyeEffect {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
