.picture-introduce { 
    background-color:#fff !important;
    .introduce-artist { 
        background-color:#fff !important;
        p { background-color:#fff !important;}
        p { font-size:75px;
        font-weight:500;
        letter-spacing:5px;
        margin-left:50px;
        margin-right: 100px;
    }
        .year { 
        font-size:50px;
        background-color:none;
        display:block;
        padding-top: 50px;
        text-align:center;
    }
    }
}

.picture-container {
    display:flex;
    align-items:center;
    // justify-content:center;
    background-color:#fff !important;
    margin: auto;
    height: 100%;
}

.picture-detail {
    display:flex;
    flex-direction:column;
    background-color:#fff !important;
    text-align:center;
    >img { height: 600px; margin-top:20px;}
    h6 {
        background-color:#fff !important;
        font-size:25px;
        font-weight:400;
        display:block;
        margin-top: 20px;
    }
    p { 
        font-size: 22px;
        font-weight: 100;
        margin-top: 20px;
        background-color:#fff !important;
    }
}
.picture-description {
    margin-left: 50px;
    p {
        background-color:#fff !important;
        width: 400px;
        line-height:50px;
        font-weight:bold;
    }
}
.background-white { background-color:#fff !important;}

.picture-slider { 
    display:flex;
    align-items:center;
    justify-content:center;
    width: 1800px;
    margin:auto;
    h6,p { background-color:#fff !important;}
    p { line-height:50px;}
    .detail { font-size:15px; font-weight: bold;}
    img { width: 800px; height: 100vh; display:block; margin-left:16px}
}

.art-box > .small-size{
    left:150px;
    p{ font-size:22px;}
    h1 { font-size:90px;}
}

.picture-introduce >.picture-container .picture-see {
    animation: blink-effect 2s step-end infinite;
    font-weight:bold;
    font-size:30px;
}

.container { width: 25000px !important;}

@keyframes blink-effect {
    0% {
        opacity: 1;
        background-color:#fff ;
      }
    50% {
      opacity: 0;
      background-color:#fff ;
    }
    100% {
        opacity: 1;
        background-color:#fff;
      }
  }
  

@keyframes loadEffect1 {
    0%{
        opacity: 0;
        transform:translateX(0px);
        display:none;
    }
    100%{
        opacity: 1;
        transform:translateX(79px);
    }
}

@keyframes fadeEffect1 {
    0% {
        opacity:1;
        transform:translateX(0px);
        display:block;
    }
    100% {
        opacity:0;
        transform:translateX(-79px);
    }
}

@keyframes eyeEffect {
    0% {
        opacity:0;
    }
    100% {
        opacity:1;
    }
}